@import "master";

@media (min-width: 1620px) {
  .hero-section {
    background-size: cover;
  }
}
@media (max-width: 1200px) {
  .hero-section {
    background-size: cover;
  }
}
@media (max-width: 992px) {
  .main-header {
    background-color: #0f1323;
  }
  .navbar-inner {
    &__bottom {
      background: #0f1323;
    }
  }
  .page-title {
    font-size: 2em;
  }
  .section {
    &-title {
      font-size: 2em;
    }
    &-subtitle {
      font-size: 1.5em;
    }
  }
  .hero-section {
    background-image: url(../assets/hero-bg-mobile.png);
    background-position: center top;
  }
  .contact-form {
    &__btn {
      width: 100%;
    }
  }
}
@media (max-width: 768px) {
  .feature {
    min-height: 9em;
  }
  .prices-table {
    &__btn {
      width: 100%;
    }
  }
  .individual-order-feature {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .branches-table__content {
    [class*="col-"] {
      &:first-child {
        font-weight: bold;
        color: $black;
      }
    }
  }
}
@media (max-width: 576px) {
  body {
    font-size: 14px;
  }
}
